export default {
    props: {
        typeList: {
            type: Object,
            default: {
                name: '',
                id: '',
                selected: false,
                children: [{
                    name: '',
                    id: '',
                    selected: false
                }]
            }
        },
        infoId: {
            type: Number,
            default: 0
        },
        other: {
            type: [Object, Number, String, Array, Boolean],
            default: ''
        }
    },
    data() {
        return {
            selected: [],
            checked: false
        }
    },
    beforeMount() {
        this.getTypeList();
    },
    methods: {
        getTypeList() {
            if (this.typeList.children && this.typeList.children.length) {
                this.typeList.children.forEach(val => {
                    val.selected ? this.selected.push(val) : ''
                });
                this.checked = (this.typeList.children.length == this.selected.length);
            } else {
                this.checked == !!this.typeList.selected;
            } 
            if (this.selected.length) {
                this.$emit('change', {
                    index: this.infoId,
                    list: [{
                        name: this.typeList.name,
                        id: this.typeList.id
                    }, ...this.selected]
                })
            } else {
                this.$emit('change', {
                    index: this.infoId,
                    list: []
                })
            }
        },
        clearKey(val) { //全选或全清理
            this.checked = !this.checked;
            if (this.checked) {
                this.selected = [...this.typeList.children];
            } else {
                this.selected = [];
            }
            this.$emit('change', {
                index: this.infoId,
                list: this.checked ? [{
                    name: this.typeList.name,
                    id: this.typeList.id
                }, ...this.selected] : []
            });
        },
        setKey(val) {
            let index = this.selected.indexOf(val);
            if (index > -1) {
                this.selected.splice(index, 1);
            } else {
                this.selected.push(val);
            }
            this.checked = this.selected.length == this.typeList.children.length;
            if (this.selected.length) {
                this.$emit('change', {
                    index: this.infoId,
                    list: [{
                        name: this.typeList.name,
                        id: this.typeList.id
                    }, ...this.selected]
                })
            } else {
                this.$emit('change', {
                    index: this.infoId,
                    list: []
                })
            }
        }
    }
}
